//–¢–µ—Å—Ç
class Test {
  constructor(questions, resuits) {
    this.questions = questions;
    this.results = results;
    this.result = 0;
    this.score = 0;
    this.currentQuestion = 0;
  }

  //–î–æ–±–∞–≤–ª–µ–Ω–∏–µ –æ—á–∫–æ–≤
  Click(index) {
    let value = this.questions[this.currentQuestion].Click(index);
    this.score += value;

    let correct = -1;
    if (value >= 1) {
      correct = index;
    } else {
      for (
        let i = 0;
        i < this.questions[this.currentQuestion].answers.length;
        i += 1
      ) {
        if (this.questions[this.currentQuestion].answers[i].value >= 1) {
          correct = i;
          break;
        }
      }
    }
    this.Next();
    return correct;
  }

  //–ü–µ—Ä–µ—Ö–æ–¥ –∫ —Å–ª–µ–¥—É—é—â–µ–º—É –≤–æ–ø—Ä–æ—Å—É
  Next() {
    this.currentQuestion += 1;
    if (this.currentQuestion >= this.questions.length) {
      this.End();
    }
  }

  //–ü–æ–ª—É—á–µ–Ω–∏–µ —Ä–µ–∑—É–ª—å—Ç–∞—Ç–∞
  End() {
    for (let i = 0; i < this.results.length; i += 1) {
      if (this.results[i].Check(this.score)) {
        this.result = i;
      }
    }
  }
}

//–í–æ–ø—Ä–æ—Å—ã —Ç–µ—Å—Ç–∞
class Question {
  constructor(type, text, picture, answers) {
    this.type = type;
    this.text = text;
    this.picture = picture;
    this.answers = answers;
  }

  Click(index) {
    return this.answers[index].value;
  }
}

//–û—Ç–≤–µ—Ç—ã —Ç–µ—Å—Ç–∞
class Answer {
  constructor(text, value) {
    this.text = text;
    this.value = value;
  }
}

//–†–µ–∑—É–ª—å—Ç–∞—Ç—ã —Ç–µ—Å—Ç–∞
class Result {
  constructor(text, value) {
    this.text = text;
    this.value = value;
  }

  //–ü—Ä–æ–≤–µ—Ä–∫–∞ –æ—á–∫–æ–≤ –¥–ª—è –ø–æ–ª—É—á–µ–Ω–∏—è —Ä–µ–∑—É–ª—å—Ç–∞—Ç–∞
  Check(value) {
    if (this.value <= value) {
      return true;
    } else {
      return false;
    }
  }
}

